.partner-about {
    padding-top: 70px;
    padding-bottom: 0;

    font-family: $segoe;
    color: $darktext;
}

.partner-about__heading {
    margin-bottom: 70px;
}

.partner-about__types {
    max-width: 1024px;
    margin: 0 auto;
    display: flex;
}

.partner-about__type {
    width: 44%;
    margin-right: 6%;
    display: flex;
    flex-flow: column nowrap;

    &:last-child {
        width: 50%;
        margin-right: 0;
    }
}

.partner-about__type-image {
    position: relative;
    width: 85px;
    height: 85px;
    margin: 0 0 25px;

    svg {
        position: absolute;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50%, -50%);
        bottom: 0;
        width: 100%;
        max-width: 100%;
        max-height: 100%;
    }
}

.partner-about__type-title {
    margin-bottom: 30px;

    font-size: 32px;
    font-weight: 700;
    line-height: 1.25;
}

.partner-about__type-subtitle {
    margin-top: -25px;

    font-size: 14px;
    font-style: italic;
    line-height: 1.3;
}

.partner-about__type-text {
    margin-top: auto;

    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;

    p {
        margin: 0;
        margin-bottom: 25px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.partner-about__steps {
    max-width: 865px;
    margin: 0 auto;
    padding-top: 90px;
}

.partner-about__steps-title {
    display: block;
    margin: 0 auto 110px;

    font-size: 32px;
    font-weight: 700;
    line-height: 1.25;
    text-align: center;

    .text {
        position: relative;
        display: inline-block;

        &::before,
        &::after {
            content: '';
            position: absolute;
            width: 1px;
            height: 96px;
            left: 50%;
            bottom: -96px;
        }

        &::before {
            background-color: #3c7bd8;
            transform: translateX(calc(50% - 3px));
        }

        &::after {
            background-color: #5fce67;
            transform: translateX(calc(50% + 3px));
        }
    }

    .line {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);

        &.line-before {
            color: #3c7bd8;
            left: -17px;
        }

        &.line-after {
            color: #5fce67;
            right: -17px;

            &::before {
                right: auto;
                left: 0;
            }

            &::after {
                right: -72px;
                left: auto;
            }
        }

        &::before,
        &::after {
            content: '';
            position: absolute;
            
            background-color: currentColor;
        }

        &::before {
            width: 72px;
            height: 2px;
            right: 0;
        }

        &::after {
            left: -72px;
            width: 2px;
            height: 88px;
            bottom: 0;
        }
    }
}

.partner-about__step-title {
    padding: 15px;

    opacity: 0.4;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-align: center;
    text-transform: uppercase;

    &:first-child {
        padding-top: 0;
    }
}

.partner-about__step-row {
    padding: 80px 0;
    display: flex;
    flex-flow: row nowrap;

    &:last-child {
        padding: 0;
    }
}

.partner-about__step-block {
    position: relative;
    margin: 0 8px;
    width: 45%;
    flex: 1 1 auto;
    min-height: 126px;
    padding: 30px 40px;

    display: flex;

    background-color: #ffffff;

    &--full {
        width: 100%;

        box-shadow: 0 5px 30px rgba(60, 190, 216, 0.1);
    }
    
    &--blue {
        box-shadow: 0 5px 30px rgba(60, 123, 216, 0.1);

        &::before,
        &::after {
            background-image: url('../images/partner/blue-path.svg');
            right: -5px;
        }
    }

    &--green {
        box-shadow: 0 5px 30px rgba(107, 174, 112, 0.1);

        &::before,
        &::after {
            background-image: url('../images/partner/green-path.svg');
            left: -5px;
        }
    }

    &::before,
    &::after {
        content: '';
        position: absolute;
        width: 269px;
        height: 80px;

        background-size: contain;
        background-repeat: no-repeat;
    }

    &::before {
        top: -80px;
    }

    &::after {
        bottom: -80px;
        transform: rotateY(180deg);
    }
}

.partner-about__step-content {
    margin: auto 0;
    width: 100%;
}

.partner-about__step-line {
    margin-bottom: 25px;

    &--flex {
        display: flex;
        align-items: center;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.partner-about__step-benefit {
    width: 25%;
    flex: 0 0 auto;
    margin-right: 15px;

    text-align: right;
    font-size: 40px;
    font-weight: 700;
}

.partner-about__step-text {
    margin: 0;
    flex: 1 1 auto;

    font-size: 14px;
    font-weight: 400;
    line-height: 1.3;

    &--medium {
        font-size: 16px;
    }

    &--big {
        font-size: 24px;
        text-align: center;
    }

    b {
        display: block;
        text-align: center;
        font-weight: 700;
    }
}